.header_container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 31%;
}

.header_title {
  position: relative;
  font-size: 72px;
  flex-grow: 0;
}
.header_title::before {
  content: "";
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 12px;
  bottom: 0;
  background: linear-gradient(to right, #006f66, #2fb9ae);
}
