.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.questionPropositionContainer {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  flex-direction: column-reverse;
  background: #006f66;
}

.questionPropositionContainer::before {
  content: "";
  position: absolute;
  z-index: 8;
  left: 0;
  top: 50%;
  width: 100%;
  height: 37px;
  background: #054a44;
}

.proposition {
  position: absolute;
  text-align: center;
  color: white;
  font-size: 96px;
  top: 41%;
  width: 60%;
  z-index: 10;
  font-family: "Paytone One";
  -webkit-box-shadow: 0px 0px 30px 5px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 0px 30px 5px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 0px 30px 5px rgba(0, 0, 0, 0.25);
  background: #006f66;
}

button {
  font-size: 96px;
  font-family: "Paytone One";
  cursor: pointer;
  color: white;
  background: #006f66;
  border: none;
  width: 100%;
  height: 100%;
  -webkit-box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
  box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
}

button:hover {
  background: #054a44;
}
