@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Paytone+One&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Montserrat", sans-serif;
}

.container_score {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.question_number {
  border: 12px solid #006f66;
  font-size: 96px;
}

.score {
  font-size: 36px;
}

.header_container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 31%;
}

.header_title {
  position: relative;
  font-size: 72px;
  flex-grow: 0;
}
.header_title::before {
  content: "";
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 12px;
  bottom: 0;
  background: linear-gradient(to right, #006f66, #2fb9ae);
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.questionPropositionContainer {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  flex-direction: column-reverse;
  background: #006f66;
}

.questionPropositionContainer::before {
  content: "";
  position: absolute;
  z-index: 8;
  left: 0;
  top: 50%;
  width: 100%;
  height: 37px;
  background: #054a44;
}

.proposition {
  position: absolute;
  text-align: center;
  color: white;
  font-size: 96px;
  top: 41%;
  width: 60%;
  z-index: 10;
  font-family: "Paytone One";
  box-shadow: 0px 0px 30px 5px rgba(0, 0, 0, 0.25);
  background: #006f66;
}

button {
  font-size: 96px;
  font-family: "Paytone One";
  cursor: pointer;
  color: white;
  background: #006f66;
  border: none;
  width: 100%;
  height: 100%;
  box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
}

button:hover {
  background: #054a44;
}


.container_footer {
  height: auto;
}

.question {
  font-size: 48px;
  text-align: center;
  color: white;
  background: #054a44;
}

.application {
  height: 100vh;
}



